import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

	import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the NuevoSocioPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-nuevo-socio',
  templateUrl: 'nuevo-socio.html',
})
export class NuevoSocioPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public viewCtrl :ViewController, public loadingController:LoadingController, private alertCtrl: AlertController, public formBuilder: FormBuilder, public modalCtrl : ModalController, public storage: Storage, public events: Events) {

       this.slideOneForm = formBuilder.group({
			firstName: ['', Validators.compose([Validators.maxLength(10), Validators.pattern('^[0-9]{1,45}$'), Validators.required])],
			lastName: ['', Validators.compose([Validators.maxLength(10), Validators.pattern('^[0-9]{1,45}$'), Validators.required])]
	    });



  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NuevoSocioPage');
  }



  public closeModal(){
    this.viewCtrl.dismiss();
}


}



